import { getPageProps, PageProps } from '@uniformdev/common-client';
import { getNextConfig } from '.';
import { createConsoleLogger } from './createConsoleLogger';

// nextjs v9.4+ applies `fetch` polyfill (https://nextjs.org/blog/next-9-4#improved-built-in-fetch-support)

export async function getNextPageProps(arg: { asPath: string }): Promise<PageProps> {
    // this can be used during next build process so it must not validate config with parseUniformConfig
    return await getPageProps(fetch, arg.asPath, getNextConfig(), createConsoleLogger());
}
